import { createStore } from 'vuex'
import { Add, Buttons } from '@/services/api.js'

export default createStore({
  state() {
    return {
      tarefa: null,
      botoes: null,
    }
  },
  getters: {
    getTarefa(state) {
      return state.tarefa;
    },
    getBotoes(state) {
      return state.tarefa;
    }
  },
  mutations: {
    setTarefa(state, data) {
      state.tarefa = data;
    },
    setBotoes(state, data) {
      state.botoes = data;
    }
  },
  actions: {
    async fetchAddTarefa({ commit }, payload) {
      const response = await Add.post('', {
        nome: payload.nome,
        tipo: payload.tipo
      });
      commit('setTarefa', response.data);
    },
    async fetchBotoes({ commit }) {
      console.log('entrou fetch botoes!');
      const response = await Buttons.get();
      console.log(response);
      commit('setBotoes', response.data);
    }
  },
  modules: {
  }
})
