<template>
    <div class="container">
        <p>nome</p>
        <p>data</p>
        <button @click="atualizaBotoes">botao</button>
        <pre>{{ botoes }}</pre>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    name: 'mainInfo',
    data() {
        return {
            botoes: 'teste',      
        }
    },
    computed: {
        ...mapGetters({
           Xbotoes: 'getBotoes', 
        }),
    },
    methods: {
        atualizaBotoes() {
            this.$store.dispatch('fetchBotoes');
            this.botoes = Xbotoes;
        },
    },
}
</script>